.scrollmenu
{
    background-color: #333;
    overflow: auto;
    white-space: nowrap;
}
.scrollmenu a {
    display: inline-block;
    color: white;
    text-align: center;
    padding: 14px;
    text-decoration: none;
}
.scrollmenu a:hover {
    background-color: #777;
}

.scrollmenu::-webkit-scrollbar {
    display: none;
}

.scrollmenu {
  -ms-overflow-style: none;  
  scrollbar-width: none;  
}
.lecture
{
    margin-top: 4%;
    width: 44%;
    margin-left: 4%;
    display: inline-block;
}